import {useEffect, useState} from 'react';
import {createId} from '../lib/createId';
import {useUpdate} from './useUpdate';

type TagsProps = {
  id: number, iconName: string, name: string
};

const useTags = () => {
  const [tags, setTags] = useState<TagsProps[]>([]);

  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]');
    if (localTags.length === 0) {
      localTags = [
        {id: createId(), iconName: '1', name: ''},
        {id: createId(), iconName: '2', name: ''},
        {id: createId(), iconName: '3', name: ''},
        {id: createId(), iconName: '4', name: ''},
        {id: createId(), iconName: '5', name: ''},
        {id: createId(), iconName: '6', name: ''},
        {id: createId(), iconName: '7', name: ''},
        {id: createId(), iconName: '8', name: ''},
        {id: createId(), iconName: '9', name: ''},
        {id: createId(), iconName: '10', name: ''},
        {id: createId(), iconName: '11', name: ''},
      ];
    }
    setTags(localTags);
  }, []); // 
  useUpdate(() => {
    window.localStorage.setItem('tags', JSON.stringify(tags));
  }, [tags]);

  // tag
  const findTag = (id: number) => {
    return tags.filter(tag => tag.id === id)[0];
  };
  // tag
  const findTagIndex = (id: number) => {
    let result = -1;
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].id === id) {
        result = i;
        break;
      }
    }
    return result;
  };

  // tag
  const updateTag = (id: number, obj: { name: string, iconName: string }) => {
    return setTags(tags.map(tag => tag.id === id ? {id, name: obj.name, iconName: obj.iconName} : tag));
  };

  // 
  const deleteTag = (id: number) => {
    return setTags((tags) => tags.filter(tag => tag.id !== id));
  };

  // 
  const addTag = (name: string, iconName: string) => {
    if (name !== null && name !== '') {
      return setTags([...tags, {id: createId(), name, iconName}]);
    }
  };

  // tag
  const getName = (id: number) => {
    const tag = tags.filter(tag => tag.id === id)[0];
    return tag ? tag.name : '';
  };
  // tag
  const getIcon = (id: number) => {
    const tag = tags.filter(tag => tag.id === id)[0];
    return tag ? tag.iconName : '9999';
  };

  return {tags, setTags, findTag, updateTag, findTagIndex, deleteTag, addTag, getIcon, getName};
};


export {useTags};